/*
Location通用項目
*/
.select-style {
  min-width: 60px; }
  .select-style label {
    font-size: 24px;
    line-height: 30px;
    margin-right: 20px; }

.storeitem {
  height: 45px;
  border: 1px solid #CED4DA;
  line-height: 26px;
  font-size: 18px;
  margin-bottom: 32px; }

.store-list {
  display: flex;
  flex-wrap: wrap; }

.store-item {
  width: 30%;
  margin: 0 2.7% 2.7% 0; }
  .store-item h3 {
    line-height: 36px;
    font-size: 24px;
    padding: 12px 0 12px 24px;
    border: 1px solid #00000020; }

.storecube {
  height: 224px; }
  .storecube img {
    height: 100%;
    width: 100%;
    object-fit: cover; }

.store-info {
  border: 1px solid #00000020; }
  .store-info li {
    line-height: 30px;
    font-size: 20px;
    margin: 7% 7%; }
    .store-info li img {
      height: 18px; }
    .store-info li a {
      text-decoration: none;
      color: #000; }
      .store-info li a:hover {
        color: #CED4DA; }

.storelink {
  display: block;
  color: #fff;
  background: #000;
  width: 100%;
  text-align: center;
  padding: 20px 0;
  text-decoration: none;
  line-height: 27px;
  font-size: 19px; }
  .storelink:hover {
    color: #CED4DA; }

@media (max-width: 992px) {
  .select-style {
    min-width: 64px; }
    .select-style label {
      font-size: 16px; }
  .storeitem {
    line-height: 24px;
    font-size: 16px;
    margin-bottom: 24px; }
  .store-item {
    width: 45%;
    margin: 0 2.7% 2.7% 0; }
    .store-item h3 {
      line-height: 24px;
      font-size: 16px;
      padding: 8px 0 8px 24px; }
  .store-info li {
    line-height: 21px;
    font-size: 14px; }
    .store-info li img {
      height: 15px; }
  .storelink {
    font-size: 14px;
    line-height: 21px;
    padding: 15.5px 0; }
  .storecube {
    height: 164px; } }

@media (max-width: 767px) {
  .select-style label {
    font-size: 14px; }
  .storeitem {
    line-height: 24px;
    font-size: 16px;
    margin-bottom: 24px; }
  .store-item {
    width: 90%;
    margin: 0 2.7% 2.7% 0; }
    .store-item h3 {
      padding: 8px 0 8px 24px; }
  .storecube {
    height: 164px; } }

